const ADD_POST = "ADD-POST";
const UPDATE_NEW_POST_TEXT = "UPDATE-NEW-POST-TEXT";

let initialState = {
    myPostArr: [
        { id: '1', message: 'Hello', likesCount: '23' },
        { id: '2', message: 'acnknknkdacv ', likesCount: '53' },
        { id: '3', message: 'Hsfv fsdf', likesCount: '3' },
        { id: '4', message: 'yoyo', likesCount: '93' },
        { id: '5', message: 'hehEEEhnsncs', likesCount: '193' },
        { id: '6', message: 'Hey im here', likesCount: '1193' }
    ],
    newPostText: ''
}

const profileReducer = (state=initialState, action) => {

    switch (action.type) {
        case ADD_POST:
            let newPost = {
                id: "7", message: state.newPostText, likesCount: 88
            }
            state.myPostArr.push(newPost);
            state.newPostText = "";
            return state;

        case UPDATE_NEW_POST_TEXT:
            state.newPostText = action.newText;
            return state;

        default:
            return state;

    }

}

export const addPostActionCreator = () => ({ type: ADD_POST }) //krchat f-aji greu dzev-returna anum vorpes obj aranc{} yev return bari

export const updateNewPostTextActionCreator = (text) =>
    ({ type: UPDATE_NEW_POST_TEXT, newText: text });
    
export default profileReducer;