import React from 'react';
import { sendMessageCreator, updateNewMessageBodyCreator } from '../../redux/dialogs-reducer';
import DialogItem from './DialogItem/DialogItem';
import dia from "./Dialogs.module.css";
import Message from './Message/Message';

const Dialogs = (props) => {

    let state = props.store.getState().dialogsPage;
    let newNamesArr = state.namesArr.map((el) => {
        return <DialogItem name={el.name} number={el.id} />
    });
    let newMessArr = state.messageArr.map((ell) => {
        return <Message text={ell.message} />
    });
    let newMessageBody = state.newMessageBody;

    let onSendMessageClick = () => {
        props.store.dispatch(sendMessageCreator())

    }
    let onNewMessageChange = (e) => { //kam or event
        let body =  e.target.value; 
        props.store.dispatch(updateNewMessageBodyCreator(body));                          

    }

    return (
        <div className={dia.dDiv}>

            <div className={dia.dialogsItems}>
                {newNamesArr}
            </div>
            <div className={dia.messages}>

                <div>{newMessArr}</div>
                <div>
                     <div>
                         <textarea 
                         onChange = {onNewMessageChange}
                         value={newMessageBody} 
                         placeholder="Type something..."/>
                    </div>
                    <div>
                        <button onClick={onSendMessageClick} >Add text</button>
                    </div>
                </div>
            </div>

        </div>
    );

}
export default Dialogs;